export default {
  data() {
    return {
      eye: -1,
      textarea: [],
      hotel: "",
      datas: {},
    };
  },
  mounted() {
    this.$axios({
      url: "/hotels/comments",
      params: {
        hotel: this.$route.query.id,
      },
    }).then((res) => {
      console.log(res);
      this.datas = res.data.data;
      console.log(this.datas);
    });
  },
  methods: {
    focuss(index) {
      this.eye = index;
    },
    blurs() {
      this.eye = -1;
    },
  },
};
